*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::after{
    box-sizing: border-box;
}
*::before{
    box-sizing: border-box;
}
#div1{
    position: fixed;
    transform: translateX(-50%);
    left: 50%;
    top: 20px;
}
#div1::before{
    content:"";
    display: block;
    position: absolute;
}
#div1::after{
    content:"";
    display: block;
    position: absolute;
}
@media (max-width: 500px){
    #html{
        height: 50vh;
        overflow: auto;
    }
    #div1Wrapper{
        height: 50vh;
    }
    #div1{
        position: relative;
        top: 0;
        right: 0;
    }
}